<template>

  <div style="width: 1000px;margin: 0 auto;">
    <el-menu :default-active="this.$route.path" router mode="horizontal">   <!--导航栏-->
      <el-menu-item v-for="(item,i) in navList" :key="i" :index="item.name">
        {{ item.navItem }}
      </el-menu-item>
    </el-menu>

    <div class="background_logo" :style="backgroundDiv"></div>

    <div class="block">   <!--数据范围选择-->
      <span class="demonstration">选择日期：</span>
      <el-date-picker
        v-model="time_value" type="date" placeholder="选择日期" format="yyyy-MM-dd " value-format="yyyy-MM-dd"
        @change="data_change()">
      </el-date-picker>
      <span class="demonstration">选择数据：</span>
      <el-select v-model="value" placeholder="请选择">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
      <el-button>查询</el-button>
    </div>
    <div class="map_info" style="width: 400px;float: right">
      <el-tag type="info">时间</el-tag>
      <el-tag type="info" style="width: 300px">{{time_value}}</el-tag>
      <el-tag type="info">区域</el-tag>
      <div class="eddy_address">
        <div>
          north:
          <el-tag type="info">{{eddy_data.data.areaCoordinate.latMax}}</el-tag>
        </div>
        <div>
          west:
          <el-tag type="info">{{eddy_data.data.areaCoordinate.lonMin}}</el-tag>
          &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp east:
          <el-tag type="info">{{eddy_data.data.areaCoordinate.lonMax}}</el-tag>
        </div>
        <div>
          south:
          <el-tag type="info">{{eddy_data.data.areaCoordinate.latMin}}</el-tag>
        </div>
      </div>
      <el-tag type="info">数据</el-tag>
      <el-tag type="info" style="width: 300px">卫星高度计</el-tag>
      <el-tag type="info">数量</el-tag>
      <el-tag type="info" style="width: 300px">{{eddy_data.data.lstEddies.length}}</el-tag>
      <el-tag type="info">中心点</el-tag>
      <el-tag type="info" style="width: 300px">
        ({{eddy_data.data.areaCoordinate.areaCenterLon}},{{eddy_data.data.areaCoordinate.areaCenterLat}})
      </el-tag>
      <el-tag type="info">强度</el-tag>
      <el-tag type="info" style="width: 300px">{{eddy_data.data.lstEddies[0].shapeLons[0]}}</el-tag>
    </div>

    <div style="width:500px;">
      <div id="firePowerMap" style="width: 500px;height: 500px"></div>   <!--百度地图-->
      <div style="text-align: center;padding: 20px 0">
        <el-tag>正向涡旋</el-tag>
        <el-tag type="danger">反向涡旋</el-tag>
      </div>
    </div>

    <!--    <div style="text-align: left;padding: 30px 50px">-->
    <!--      <div class="block">   &lt;!&ndash;数据范围选择&ndash;&gt;-->
    <!--        <span class="demonstration">选择日期：</span>-->
    <!--        <el-date-picker v-model="time_value" type="date" placeholder="选择日期" @change="data_change()">-->
    <!--        </el-date-picker>-->
    <!--      </div>-->
    <!--      latitude 2 &#45;&#45; 40<br>longitude 104 &#45;&#45; 132-->
    <!--    </div>-->
    <!--    <div id="1" style="height: 356px;width:400px;border:2px solid #000;">   &lt;!&ndash;数据展示&ndash;&gt;-->
    <!--      点击右边涡旋获取数据-->
    <!--    </div>-->
  </div>
</template>

<script>
  export default {
    name: "History",
    methods: {
      _getMapData() {
        let map = new BMap.Map("firePowerMap");  // 创建Map实例
        let point = new BMap.Point(118.0, 22.0);
        map.centerAndZoom(point, 5);
        map.enableScrollWheelZoom(true);
        // map.disableScrollWheelZoom();//禁止滚动缩放
        map.disableDoubleClickZoom();//禁止双击缩放
        window.map = map;//将map变量存储在全局
      },
      eddy_show() {
        for (let j = 0; j < this.eddy_data.data.lstEddies.length; j++) {
          let a1 = [];
          for (let i = 0; i < this.eddy_data.data.lstEddies[j].shapeLons.length; i++) {
            let b = new BMap.Point(this.eddy_data.data.lstEddies[j].shapeLons[i], this.eddy_data.data.lstEddies[j].shapeLats[i]);
            a1.push(b);
          }
          if (this.eddy_data.data.lstEddies[j].direction === 1) {
            var polygon = new BMap.Polygon(a1, {strokeColor: "blue", strokeWeight: 2, strokeOpacity: 0.5});  //创建蓝色多边形
          } else {
            var polygon = new BMap.Polygon(a1, {strokeColor: "red", strokeWeight: 2, strokeOpacity: 0.5});  //创建红色多边形
          }
          polygon.name = this.eddy_data.data.lstEddies[j].name;
          polygon.longitude = this.eddy_data.data.lstEddies[j].longitude;
          polygon.latitude = this.eddy_data.data.lstEddies[j].latitude;
          polygon.radius = this.eddy_data.data.lstEddies[j].radius;
          polygon.onclick = function () {
            document.getElementById('1').innerHTML = 'name:' + this.name + '<br>' + 'longitude:' + this.longitude + '<br>' + 'latitude:' +
              this.latitude + '<br>' + 'radius:' + this.radius;
          }
          map.addOverlay(polygon);
        }
      },
      get_data(datetime) {
        let that = this;
        console.log(datetime);
        this.axios.post('http://159.226.158.119:8072/eddy/records' + '?dateString=' + datetime, {
          //参数
        })
          .then(function (response) {
            that.eddy_data = response.data;
            map.clearOverlays()
            that.eddy_show();
            let point = new BMap.Point(that.eddy_data.data.areaCoordinate.areaCenterLon, that.eddy_data.data.areaCoordinate.areaCenterLat);
            map.centerAndZoom(point, 5);
            console.log(datetime)
          })
          .catch(function (error) {
            console.log(error);
          });
      },
      data_change() {

        let datetime = this.time_value;
        console.log(datetime);
        this.get_data(datetime);
      }
    },
    created() {
      this.get_data();
    },
    mounted() {
      this._getMapData();

    },
    data() {
      return {
        backgroundDiv: {
          backgroundImage: 'url(' + require('@/assets/img/中尺度logo.png') + ')',
          backgroundRepeat: 'no-repeat',
          backgroundSize: '100% 100%',
          backgroundPosition: 'center center'
        },
        options: [{
          value: '选项1',
          label: '高度计'
        }, {
          value: '选项2',
          label: '高度计'
        }],
        value: '',
        time_value: '2020-09-23',
        navList: [
          {name: '/', navItem: '最新涡旋'},
          {name: '/History', navItem: '历史涡旋'},
          {name: '/Explore', navItem: '涡旋探测'},
        ],
        eddy_data: {
          "status": 0,
          "data": {
            "dateString": "20200815",
            "areaCoordinate": {
              "areaCenterLat": "22.0",
              "areaCenterLon": "118.0",
              "latMin": "2",
              "latMax": "42",
              "lonMin": "104",
              "lonMax": "132"
            },
            "lstEddies": [
              {
                "name": "track_579",
                "longitude": "131.52621",
                "latitude": "40.62422",
                "direction": 1,
                "radius": "74858.914",
                "shapeLons": [
                  "131.125",
                ],
                "shapeLats": [
                  "39.864620208740234",
                ]
              }
            ]
          }
        },
      }
    },
  }
</script>

<style scoped>

</style>



