        {{ item.navItem }}
      </el-menu-item>
    </el-menu>
    <div id="firePowerMap" style="width: 500px;height: 500px;float: right"></div>
    <div style="text-align: left;padding: 30px 50px">
      日期：2020-09-15<br>latitude 2 -- 40<br>longitude 104 -- 132
    </div>
    <div id="1" style="height: 376px;width:400px;border:2px solid #000;">
      点击右边涡旋获取数据
    </div>
