        let map = new BMap.Map("firePowerMap");  // 创建Map实例
        let point = new BMap.Point(120.375, 18.642273);
        map.centerAndZoom(point, 6);
        map.disableScrollWheelZoom();//禁止滚动缩放
        map.disableDoubleClickZoom();//禁止双击缩放
        window.map = map;//将map变量存储在全局
      },
      eddy_show() {
        for (let j = 0; j < this.data.length; j++) {
          var a1 = [];
          for (let i = 0; i < this.data[j].contour_lat_e.length; i++) {
            let b = new BMap.Point(this.data[j].contour_lat_e[i], this.data[j].contour_lon_s[i]);
            a1.push(b);
          }
          var polygon = new BMap.Polygon(a1, {strokeColor: "blue", strokeWeight: 2, strokeOpacity: 0.5});  //创建多边形
          polygon.id = this.data[j].id;
          polygon.onclick = function () {
            document.getElementById('1').innerText='id:' + this.id;
          }
          map.addOverlay(polygon);
        }
      },
    },
