import Vue from 'vue'
import Router from 'vue-router'
import New from '@/components/New'
import History from '@/components/History'
import Explore from '@/components/Explore'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'New',
      component: New
    },
    {
      path: '/history',
      name: 'History',
      component: History
    },
    {
      path: '/explore',
      name: 'Explore',
      component: Explore
    },
  ]
})



